.main-nav {
    background-color: #f8f9fa;
    padding: 10px;
}

.nav-list {
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
}

.nav-item {
    margin-right: 30px;
}

.nav-link {
    text-decoration: none;
    color: #007bff;
    padding: 10px;
    display: block;
    font-size: 16px;
}

.nav-link:hover {
    text-decoration: underline;
}

.dropdown {
    position: relative;
}

.dropdown .dropdown-toggle::after {
    content: '';
}

.dropdown-menu {
    display: none;
    position: absolute;
    background-color: #ffffff;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
    padding-left: 0;
}

.dropdown:hover .dropdown-menu {
    display: block;
}

.dropdown-item {
    list-style-type: none;
    padding: 5px 10px;
}

.dropdown-item .nav-link {
    padding: 10px;
    padding-right: 5px;
    padding-left: 2px;
    display: block;
}

.dropdown-item .nav-link:hover {
    background-color: #f1f1f1;
}