.lesson-container {
    max-width: 80%;
    margin: auto;
    padding: 20px;
    font-family: Arial, sans-serif;
    line-height: 1.7;
}
  
.lesson-section {
    margin-bottom: 20px;
}

.lesson-header {
    background-color: #343328;
    padding: 10px;
    color: white;
    text-align: center;
}

.lesson-section h2 {
    color: #333;
    border-bottom: 2px solid #282c34;
    padding-bottom: 5px;
    margin-bottom: 10px;
}

.lesson-section p {
    line-height: 1.6;
}

.lesson-section pre {
    background-color: #f5f5f5;
    padding: 10px;
    border-radius: 5px;
    overflow-x: auto;
}

.lesson-section code {
    font-family: 'Courier New', Courier, monospace;
}

.lesson-section ul {
    list-style-type: disc;
    margin: 10px 20px;
    margin-left: 0;
}

.lesson-section ul li {
    margin-bottom: 10px;
}

.lesson-footer {
    background-color: #f4f4f4;
    padding: 10px;
    text-align: center;
    border-top: 1px solid #ddd;
}